<template>
  <div class="main">
    <div class="loginLog">
			<div class="searchA">
    		<el-input v-model="searchForm.keyword" placeholder="输入账号搜索" size="small" clearable></el-input>
				<el-date-picker v-model="searchForm.date" value-format="yyyy-MM-dd HH:mm:ss" type="datetimerange" size="small" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"> </el-date-picker>
    		<el-button size="small" type="primary" plain icon="el-icon-search" @click="searchClick" style="margin-left: 25px;">查询</el-button>
    	</div>
			<div class="home_table">
    		<el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
    			<template v-for="(item, index) in tableHead">
    				<el-table-column :prop="item.prop" :label="item.title" :key="index" :width="item.width" :show-overflow-tooltip="true" v-if="item.prop == 'failed_name'">
							<template slot-scope="scope">
								<span v-if="scope.row.failed == 0" style="color: #409EFF;">{{ scope.row.failed_name }}</span>
								<span v-else-if="scope.row.failed == 1" style="color: #F56C6C;">{{ scope.row.failed_name }}</span>
							</template>
						</el-table-column>
    				<el-table-column :prop="item.prop" :label="item.title" :key="index" :width="item.width" :show-overflow-tooltip="true" v-else></el-table-column>
    			</template>
    		</el-table>
    	</div>
    	<!-- <div class="pagination"> -->
    	<el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />

		</div>
  </div>
</template>
<script>
import { loginLogList } from '@/api/index';
export default {
  data() {
    return {
			total: 0,
			page: 1,
			pageSize: 15,
			isLoading: false,
			tableHeight: window.innerHeight - 180,
			tableHead: [
        { title: '编号', prop: 'id', },
        { title: '账号', prop: 'user_name', },
        { title: '登录IP', prop: 'login_ip', },
        { title: '登录状态', prop: 'failed_name', },
        { title: '操作时间', prop: 'login_time', }
      ],
			tableData: [],
			searchForm: {
				keyword: '',
				date: [],
			}

		};
  },
  mounted() {
		this.getList();
	},
  methods: {
		// 监听 pagesize 改变
  	handleSizeChange(newSize) {
  		this.page = 1;
  		this.pageSize = newSize;
  		this.getList();
  	},
  	// 监听 pagenum 改变
  	handleCurrentChange(newPage) {
  		this.page = newPage;
  		this.getList();
  	},
		searchClick() {
			this.tableData = [];
    	this.pageSize = 15;
    	this.page = 1;
			this.getList();
		},
		async getList() {
			this.isLoading = true;
			let params = {
				keyword: this.searchForm.keyword,
				start_time: this.searchForm.date[0],
				end_time: this.searchForm.date[1],
				curr: this.page,
      	pagesize: this.pageSize,
			}
			const { data } = await this.axios.post(loginLogList, params);
			if(data.code == 1) {
				this.isLoading = false;
				this.tableData = data.data.data;
        this.total = data.data.total;
			}else {
				this.isLoading = false;
				this.$message({
					showClose: true,
					message: data.msg,
					type: 'error'
				});
			}
		},
		
	},
};
</script>

<style lang="scss" scoped>
.loginLog {
  .searchA {
    display: flex;
    padding: 12px 25px;
    background: #fff;
    .el-select {
      margin-right: 24px;
    }
    .el-input {
      width: auto;
      margin-right: 24px;
    }
    .add {
      flex: 1;
      text-align: right;
    }
  }

  .dialogInput {
    display: flex;
    margin-bottom: 0px;
    .padd {
      width: 100%;
      margin-right: 20px;
      .paddTit {
        margin-bottom: 10px;
      }
    }
  }
  .btn {
    width: 250px;
  }
}
</style>



